

















































































































import UniIcon from '../../components/uni-icon/uni-icon.vue';
import { requestip } from '@/pages/api/api.js'
export default{
	compoents:{
		UniIcon
	},
	data(){
		return{
			top:[
				// {name:'淘宝'},
				{},
				{name:'拼多多'},
				{}
				// {name:'京东'}
			],
			tabIndex:1,
			data:''
		}
	},
	methods: {
		wholeIndex(i,n){
			// this.tabIndex=n;
		},
		months(i,n){
			uni.showModal({
				title:i,
				showCancel:false,
				content:n
			})
		},
		drawal(){
			uni.navigateTo({
				url:'../withdrawal/withdrawal'
			})
		},
		// 数据初始化
		wholeInit(){
			var uid=uni.getStorageSync('userId');
			uni.request({
				url: requestip()+'/v2/customer/wx/userintegraldetail?user_id='+uid,
				method: 'GET',
				header:{
					"access-token":uni.getStorageSync('access-token'),
				    "fecshop-uuid":uni.getStorageSync('Fecshop-Uuid')
				   },
				data: {},
				success: res => {
					// console.log("whole:",res.data);
					this.data=res.data.data.fans_list;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		Settlement(){
			uni.navigateTo({
				url:'../drawmoney/drawmoney'
			})
		},
		// 关闭下拉
		sotpres(){
			setTimeout(()=>{
				this.wholeInit();
				uni.stopPullDownRefresh();
			},300)
		},
	},
	onLoad:function(){
		this.wholeInit();
		uni.hideShareMenu();
	},
	// 下拉刷新监听函数
	onPullDownRefresh() {
		// console.log("onPullDownRefresh")
		this.sotpres();
	}
}
